import { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'
import { Link } from 'react-router-dom'
import { useSetRecoilState } from 'recoil'

import { Box, Button, Card, Container, Grid, Tab, Tabs, Typography, useMediaQuery, useTheme } from '@mui/material'
import '@toast-ui/editor/dist/toastui-editor.css'

import { ClubInfo } from 'src/components/ClubInfo'
import PageTitleWrapper from 'src/components/PageTitleWrapper'

import ConferencePreviewGrid from 'src/content/club/Home/ConferencePreviewGrid'
import RecruitPreviewCard from 'src/content/club/Home/RecruitPreviewCard'

import { useDeleteLikeClub, useGetClubContent, useGetClubInformation, usePostLikeClub } from 'src/hooks/api/club'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'
import { isLikeBtnClickedAtom } from 'src/recoil/atom'

import ReviewGrid from './ReviewGrid'

const Home: React.FC = () => {
  const { id } = useParams()
  const clubId = Number(id)

  const theme = useTheme()
  const isDesktopOrTablet = useMediaQuery(theme.breakpoints.up('sm'))

  const [isLike, setIsLike] = useState(false)
  const [isNotDummy, setIsNotDummy] = useState(false)
  const setIsLikeBtnClicked = useSetRecoilState(isLikeBtnClickedAtom)
  const { userInfo } = useSafeAuthUser()

  const postLikeClubMutation = usePostLikeClub(clubId)
  const deleteLikeClubMutation = useDeleteLikeClub(clubId)

  const userClub = userInfo?.clubs.find(c => c.id === clubId)
  const userAdmin = userInfo?.clubs.find(
    c => c.id === clubId && (c.permission === 'SUPERADMIN' || c.permission === 'ADMIN')
  )
  const isMember = !!userClub
  const isAdmin = !!userAdmin
  const { data: club, isFetched } = useGetClubInformation(clubId)
  const { data: clubContent } = useGetClubContent(clubId)

  const [tabValue, setTabValue] = useState(0)

  const handleTabChange = (_event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue)
  }

  useEffect(() => {
    if (isFetched && club) {
      setIsLike(club?.isliked)
      setIsNotDummy(club?.hasSuperadmin)
    }
  }, [isFetched])

  const handleLike = async () => {
    try {
      isLike ? await deleteLikeClubMutation.mutateAsync() : await postLikeClubMutation.mutateAsync()
      setIsLike(!isLike)
      setIsLikeBtnClicked(true)
    } catch (error) {}
  }

  // 공통 Tab 스타일
  const tabStyle = {
    padding: '12px 16px',
    height: 'auto',
    minHeight: 'auto',
    borderRadius: 0,
    textTransform: 'none' as const,
    fontSize: theme.typography.h4.fontSize || '1rem',
    minWidth: 'auto',
    marginRight: 0,
    color: theme.palette.text.secondary,
    '&.Mui-selected': {
      color: theme.palette.text.primary
    },
    '&:hover': {
      color: theme.palette.text.secondary,
      backgroundColor: 'transparent'
    },
    '&.Mui-selected:hover': {
      color: theme.palette.text.primary,
      backgroundColor: 'transparent'
    }
  }

  return (
    <>
      <Helmet>
        <title>{club ? club.name : '404 PAGE'}</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container>
          {club && (
            <ClubInfo
              club={club}
              handleLike={handleLike}
              isLike={isLike}
              isDesktopOrTablet={isDesktopOrTablet}
              isDummy={!isNotDummy}
            />
          )}
          <Grid item container xs={12} md={5} mt={1} display="flex" flexDirection="column" justifyContent="center">
            <Grid item display="flex" justifyContent="center">
              {isAdmin ? (
                <>
                  <Button
                    component={Link}
                    to={`/club/${clubId}/profile`}
                    variant="contained"
                    sx={{ width: '100%', height: '50%', padding: theme.spacing(0.5), marginY: 1, marginRight: '2%' }}
                  >
                    내 활동
                  </Button>
                  <Button
                    component={Link}
                    to={`/manage/${clubId}/member`}
                    variant="contained"
                    sx={{ width: '100%', height: '50%', padding: theme.spacing(0.5), marginY: 1, marginLeft: '2%' }}
                    color="success"
                  >
                    동아리 관리
                  </Button>
                </>
              ) : (
                isMember && (
                  <Button
                    component={Link}
                    to={`/club/${clubId}/profile`}
                    variant="contained"
                    sx={{ width: '100%', padding: theme.spacing(0.5), marginY: 1 }}
                  >
                    내 활동
                  </Button>
                )
              )}
            </Grid>
          </Grid>
        </Grid>
      </PageTitleWrapper>
      <Container maxWidth="md">
        {club && (
          <Grid container spacing={2}>
            <Grid item xs={12} md={6}>
              <Typography variant="h4" sx={{ m: 2 }}>
                소개글
              </Typography>
              <Card>
                {clubContent?.content ? (
                  <Box px={2} py={1}>
                    <div
                      className="toastui-editor-contents"
                      dangerouslySetInnerHTML={{ __html: clubContent?.content || '' }}
                    ></div>
                  </Box>
                ) : (
                  <Typography color="text.secondary" p={2}>
                    동아리에서 작성한 소개글이 없어요.
                  </Typography>
                )}
              </Card>
            </Grid>
            <Grid item xs={12} md={6}>
              <Typography variant="h4" sx={{ m: 2 }}>
                모집 공고
              </Typography>
              <RecruitPreviewCard clubId={clubId} isDesktopOrTablet={isDesktopOrTablet} />
            </Grid>
            <Grid item xs={12}>
              <Box sx={{ borderBottom: 0.5, borderColor: 'divider', mb: 2 }}>
                <Tabs
                  variant="fullWidth"
                  value={tabValue}
                  onChange={handleTabChange}
                  aria-label="club activity tabs"
                  centered
                  sx={{
                    // 테마의 커스텀 스타일을 기본 MUI 스타일로 리셋
                    height: 'auto',
                    minHeight: 'auto',
                    '& .MuiTabs-indicator': {
                      height: 2,
                      borderRadius: 0,
                      border: 'none',
                      boxShadow: 'none',
                      backgroundColor: 'transparent',
                      borderBottom: `2px solid ${theme.palette.text.primary}`
                    }
                  }}
                >
                  <Tab label="활동 기록" sx={tabStyle} />
                  <Tab label="활동 후기" sx={tabStyle} />
                </Tabs>
              </Box>
              {tabValue === 0 && <ConferencePreviewGrid clubId={clubId} />}
              {tabValue === 1 && <ReviewGrid clubId={clubId} name={club.name} description={club.description} />}
            </Grid>
          </Grid>
        )}
      </Container>
    </>
  )
}

export default Home
