import { Card, Grid, Typography } from '@mui/material'

import ConferencePreviewCard from 'src/components/ConferencePreviewCard'
import SuspenseLoader from 'src/components/SuspenseLoader'

import { useGetPublicConferenceRounds } from 'src/hooks/api/clubConferenceHooks'

interface ConferencePreviewGridProps {
  clubId: number
}

const ConferencePreviewGrid = ({ clubId }: ConferencePreviewGridProps) => {
  const { data: conferenceRoundsData, isLoading } = useGetPublicConferenceRounds(clubId)

  if (isLoading) {
    return <SuspenseLoader />
  }

  const conferenceRounds = conferenceRoundsData?.conferenceRounds ?? []

  if (conferenceRounds.length === 0) {
    return (
      <Card sx={{ p: 2 }}>
        <Typography color="text.secondary">아직 작성된 활동 기록이 없어요.</Typography>
      </Card>
    )
  }

  return (
    <Grid container spacing={2}>
      {conferenceRounds.map(conferenceRound => (
        <Grid item xs={12} sm={6} md={6} key={conferenceRound.id}>
          <ConferencePreviewCard club={{ id: clubId }} conferenceRound={conferenceRound} />
        </Grid>
      ))}
    </Grid>
  )
}

export default ConferencePreviewGrid
